div.jumbotron {
    margin-bottom: 0;
    background-color: #bb9ef0;
    padding: .3rem 2rem;
    background-image: linear-gradient(#e7bca4, #e7bca4, white);
}

nav {
    background-color: white;
    color: #e7bca4;
    font-weight: bold;
}
.nav-links {
    color: #e7bca4;
    padding: 1rem;
}
.nav-links:hover {
    color: white;
}

@media(max-width: 850px) {
    .logo {
        display: none;
    }
}
